import React, { Component } from 'react';
import { connect } from 'react-redux';
import './billing.css';

import MenuSidebar from '../../containers/menuSidebar';
import Header from '../../containers/header';

import { updateToggleMobileSideBar } from '../../../store/actions';

class Billing extends Component {

    constructor(props){
        super(props);
        this.state = {
            
        };

        this.closeMobileSideBar = this.closeMobileSideBar.bind(this);
    }

    closeMobileSideBar(){
        this.props.updatingToggleMobileSideBar(false);
    }

    render() {
        return (
            <div className="billing_container">
                {
                    this.props.activeMobileSideBar &&
                    <div className="bc_mobile_sidebar_overlay" onClick={this.closeMobileSideBar}></div>
                }
                <MenuSidebar/>
                <div className="bc_main">
                    <Header />
                    <div className="temp"></div>
                </div>
            </div> 
        );
    }
}

const mapStateToProps = state => ({
    config: state.config,
    userAuthData: state.userAuthData,
    activeMobileSideBar: state.activeMobileSideBar
});
  
const mapDispatchToProps = dispatch =>({
    updatingToggleMobileSideBar(flag){
        dispatch(updateToggleMobileSideBar(flag))
    }
});

export default connect(mapStateToProps, mapDispatchToProps)(Billing);
